$(document).on( "pageinit", "#page-about", function(event) {

	$(".content-about").html(function(){
    var $this = $(this);
    var lang = $this.data("lang");
    var data = Constants.getAboutNomura()[lang];
    var html=[];
    $.each(data, function(index, item) {
    	if(item.Type === 'Article' || item.Type === 'Image' || item.Type === 'Table'){
    		html.push("<div data-role='headline'>");
   			html.push("	<p>" + item.Caption + "</p>");
   			html.push("</div>");        		
    	}
   		if(item.Type === 'Article'){
   			html.push("<div class='content-text'>");
   			html.push(	item.Text);
   			html.push("</div>");
      }else if(item.Type === 'Image'){
        html.push("<div class='content-image'>");
        html.push(" <img width=" + item.Width + " src='" + item.Url  + "'>");
        html.push("</div>");            
   		}else if(item.Type === 'Table'){
 				html.push("<table id='tableAward'>");
 				$.each(item.Record, function(index, recordItem) {
	 				html.push("<tr>");
	 				html.push("	<th rowspan='2'>" + recordItem.Year + "</th>");
	 				html.push("	<td>" + recordItem.AwardTop + "</td>");
	 				html.push("</tr>");
	 				html.push("<tr>");
	 				html.push("<td class='Alternative'>" + recordItem.AwardBottom + "</td>");
	 				html.push("</tr>");	 				
 				});
 				html.push("</table>");      			
   		}else if(item.Type === 'Source'){
   			html.push("<div class='content-source'>");
   			html.push("	<div>" + item.SourceCaption + ":" + item.SourceText + "</div>");
   			html.push("	<div>" + item.DateCaption + ":" + item.DateText + "</div>");
   			html.push("</div>");
   		}
    });
    return html.join(" ");		
	});
});